<template>
  <div class="popup__wrap" :class="{ on: addPopupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup box__shadow__02">
      <div class="popup__top">
        <h4>권한 {{ popupState }}</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul>
        <li>
          <dl>
            <dt class="f__medium">권한ID</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqRole.roleId"
                :disabled="popupState === '수정'"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">권한명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqRole.roleNm"
              />
            </dd>
          </dl>
        </li>
        <li class="question__input">
          <dl>
            <dt class="f__medium">설명</dt>
            <dd>
              <textarea
                name=""
                id=""
                class="custom__input"
                v-model="reqRole.roleDsc"
              ></textarea>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">사용여부</dt>
            <dd>
              <select name="" id="" v-model="reqRole.useYn">
                <option value="">선택</option>
                <option value="Y">사용</option>
                <option value="N">미사용</option>
              </select>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="custom__btn__wrap" style="display: block">
        <button
          class="custom__btn delete"
          v-show="enableRemoveBtn"
          @click="removeOrClean"
        >
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>삭제</p>
        </button>
        <button class="custom__btn right" @click="addRole">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { addRole, updateRole, removeRole } from "@/api/role";
import { clear } from "@/utill/commom";
export default {
  props: {
    addPopupEventFlag: String,
    popupState: String,
    selectedObj: [Object, Boolean],
  },
  data() {
    return {
      reqRole: {
        roleId: "",
        roleNm: "",
        roleDsc: "",
        useYn: "",
      },
      enableRemoveBtn: false,
    };
  },
  watch: {
    popupState() {
      if (this.popupState === "수정") {
        this.reqRole = this.selectedObj;
        if (this.selectedObj.useYn === "사용") {
          this.reqRole.useYn = "Y";
        } else {
          this.reqRole.useYn = "N";
        }
      } else if (this.popupState === "추가") {
        clear(this.reqRole);
      }
    },
  },
  methods: {
    popupCloseClick() {
      this.$emit("popupClose", "Close");
    },
    addRole() {
      if (this.validateComCode()) {
        if (this.$env == "development") console.log(this.reqRole);
        if (this.popupState === "수정") {
          updateRole(this.reqRole)
            .then((response) => {
              if (response.data.status === "OK") {
                this.$toast.success("공통코드 수정이 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        } else {
          addRole(this.reqRole)
            .then((response) => {
              if (this.$env == "development") console.log(response);
              if (response.data.status === "OK") {
                this.$toast.success("공통코드 추가가 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        }
      }
    },
    validateComCode() {
      if (this.reqRole.roleId === "" || this.reqRole.roleId === null) {
        this.$toast.error("코드 아이디를 입력하세요.");
        return false;
      }
      if (this.reqRole.roleNm === "" || this.reqRole.roleNm === null) {
        this.$toast.error("코드 명을 입력하세요.");
        return false;
      }
      if (this.reqRole.roleDsc === "" || this.reqRole.roleDsc === null) {
        this.$toast.error("코드 설명을 입력하세요.");
        return false;
      }
      if (this.reqRole.useYn === "" || this.reqRole.useYn === null) {
        this.$toast.error("사용여부를 입력하세요.");
        return false;
      }
      return true;
    },
    removeOrClean() {
      if (this.popupState === "수정") {
        removeRole(this.reqRole.roleId)
          .then((response) => {
            if (this.$env == "development") console.log(response);
            if (response.data.status === "OK") {
              this.$toast.success("공통코드가 삭제되었습니다");
              this.popupCloseClick();
              this.$emit("refreshList");
            }
          })
          .catch((error) => this.$toast.error(error.message));
      } else if (this.popupState === "추가") {
        this.clear();
      }
    },
    clear() {
      clear(this.reqRole);
    },
  },
};
</script>
<style scoped>
.custom__btn.right {
  float: right;
  margin-bottom: 10px;
}
</style>
