<template>
  <div class="popup__wrap" :class="{ on: setPopupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup box__shadow__02">
      <div class="popup__top">
        <h4>권한 설정</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <div class="popupo__middle">
        <PremissionTree
          ref="funcTree"
          :data="getTreeData"
          :options="{ checkbox: true, checkOnSelect: true }"
          @node:checked="treeCheck"
          @node:unchecked="treeUnCheck"
          @node:selected="treeSelect"
        />
      </div>

      <div class="custom__btn__wrap">
        <button class="custom__btn" @click="saveFunctionsByRole">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import PremissionTree from "liquor-tree";
import { getFunctionList } from "@/api/function";
import { transformToTree, extractSelectedTreeNodeId } from "@/utill/commom";
import { getFunctionsByRole, saveFunctionsByRole } from "@/api/role";
export default {
  data() {
    return {
      treeData: this.getFunctionList(),
      functionList: [],
      roleFunctions: [],
    };
  },
  components: {
    PremissionTree,
  },
  props: {
    setPopupEventFlag: String,
    selectedObj: [Object, Boolean],
  },
  computed: {
    getTreeData() {
      return this.treeData;
    },
  },
  watch: {
    selectedObj() {
      this.refreshTreeData(this.selectedObj.roleId);
    },
  },
  /*created() {
    if (this.selectedObj.roleId != null) {
      this.getFunctionsByRole(this.selectedObj.roleId);
    }
  },*/
  methods: {
    popupCloseClick() {
      this.$emit("setPopupClose", "Close");
      this.treeData = [];
      this.roleFunctions = [];
      let vm = this;
      this.functionList.forEach(function (funcId) {
        vm.$refs.funcTree.find({ id: funcId.id }).uncheck();
      });
    },
    treeCheck() {},
    treeUnCheck() {},
    treeSelect() {},
    async getFunctionList() {
      let vm = this;
      let parents = [];
      let children = [];
      await getFunctionList().then((response) => {
        vm.functionList = response.data.response.data;
        vm.functionList.forEach(function (object) {
          object.id = object.funcId;
          object.text = object.funcNm;
          object.state = {
            selectable: true,
            selected: true,
            checkOnSelect: true,
            expanded: false,
          };
          object.children = [];
          if (object.funcHighId === "") {
            parents.push(object);
          }
        });
        children = vm.functionList.filter((obj) => {
          return obj.funcHighId !== "";
        });
      });
      transformToTree(parents, children);
      if (this.$env == "development") console.log(parents);
      return parents;
    },
    getFunctionsByRole(roleId) {
      let vm = this;
      vm.roleFunctions = [];
      return new Promise(function (resolve, reject) {
        getFunctionsByRole(roleId)
          .then((response) => {
            let funcList = response.data.response.data;
            funcList.forEach((data) => {
              if (data.useYn === "Y") {
                vm.roleFunctions.push(data.funcId);
              }
            });
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    async refreshTreeData(roleId) {
      let vm = this;
      await vm.getFunctionsByRole(roleId);
      vm.roleFunctions.forEach(function (funcId) {
        vm.$refs.funcTree.find({ id: funcId }).check();
      });
    },
    saveFunctionsByRole() {
      let vm = this;
      let saveFuncIds = [];
      let tree = vm.$refs.funcTree.findAll().tree;
      let checkedNodes = tree.checkedNodes;
      extractSelectedTreeNodeId(saveFuncIds, checkedNodes);
      saveFuncIds.forEach(function (obj, idx) {
        saveFuncIds[idx] = {
          funcId: obj,
          roleId: vm.selectedObj.roleId,
          useYn: "Y",
        };
      });
      // if (this.$env == "development") console.log(saveFuncIds);
      saveFunctionsByRole(saveFuncIds)
        .then((response) => {
          // if (this.$env == "development") console.log(response);
          if (response.data.status === "OK") {
            this.$toast.success("기능설정이 되었습니다");
            this.popupCloseClick();
          }
        })
        .catch((error) => this.$toast.error(error.message));
    },
  },
};
</script>
