<template>
  <main class="permission__management__main">
    <PremissionAddPopup
      :addPopupEventFlag="addPopupEventFlag"
      @popupClose="addPopupToggle"
      :popupState="popupState"
      :selectedObj="selectedRowObj"
      @refreshList="getRoles"
    />
    <PremissionSetPopup
      :setPopupEventFlag="setPopupEventFlag"
      @setPopupClose="setPopupToggle"
      :selectedObj="selectedRowObj"
    />
    <section class="box__shadow__01 vertical__scroll grid__wrap">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["권한 관리"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["권한 관리"].subTitle }}
      </h5>
      <div class="add__or__search__wrap">
        <ul>
          <li>
            <dl>
              <dt>사용여부</dt>
              <dd>
                <select
                  name=""
                  id=""
                  ref="useYnSelector"
                  v-model="useYnFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option value="사용">사용</option>
                  <option value="미사용">미사용</option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>권한</dt>
              <dd>
                <input
                  type="text"
                  class="custom__input"
                  placeholder="이름을 입력하세요"
                  v-model="roleNameFilter"
                />
              </dd>
            </dl>
          </li>
          <li>
            <button class="custom__btn" @click="gridDataFiltering">
              <span>
                <img
                  src="@/assets/images/icon/ic-search.svg"
                  alt="검색 아이콘"
                />
              </span>
              <p>검색</p>
            </button>
          </li>
        </ul>
        <button class="custom__btn" @click="addPopupToggle('추가')">
          <span>
            <img src="@/assets/images/icon/ic-plus.svg" alt="추가 아이콘" />
          </span>
          <p>권한 추가</p>
        </button>
      </div>
      <div class="grid__wrap" @click="rowClick($event)">
        <Grid
          class="on"
          ref="gridEl"
          :cols="cols"
          :pagination="true"
          :sort="true"
          :rows="filteredGridData"
        ></Grid>
      </div>
    </section>
  </main>
</template>
<script>
import "@/assets/css/permissionManagement.css";
import PremissionAddPopup from "@/components/SystemManagement/PermissionManagement/PremissionAddPopup";
import PremissionSetPopup from "@/components/SystemManagement/PermissionManagement/PremissionSetPopup";
import { Grid } from "gridjs-vue";
import { getRoleList } from "@/api/role";
export default {
  data() {
    return {
      addPopupEventFlag: "Close",
      setPopupEventFlag: "Close",
      popupState: "",
      selectedRowObj: "",
      cols: [
        { name: "번호", id: "index" },
        { name: "권한명", id: "roleNm" },
        { name: "권한코드", id: "roleId" },
        { name: "설명", id: "roleDsc" },
        { name: "사용여부", id: "useYn" },
        { name: "설정", id: "setIcon" },
      ],
      roleList: [],
      useYnFilter: "사용",
      roleNameFilter: "",
      filteredGridDataBack: [],
      filteredGridData: [],
    };
  },
  components: {
    Grid,
    PremissionAddPopup,
    PremissionSetPopup,
  },
  computed: {
    getPermissionList() {
      return this.roleList;
    },
  },
  created() {
    this.getRoles();
  },
  methods: {
    addPopupToggle(evtName) {
      if (evtName === "추가") this.selectedRowObj = false;
      this.popupState = evtName;
      this.addPopupEventFlag === "Close"
        ? (this.addPopupEventFlag = "Open")
        : (this.addPopupEventFlag = "Close");
    },
    setPopupToggle() {
      this.setPopupEventFlag === "Close"
        ? (this.setPopupEventFlag = "Open")
        : (this.setPopupEventFlag = "Close");
    },
    rowClick(param) {
      const selectedObjIndex = Number(param.path[1].childNodes[0].textContent);
      const selectedCell = param.path[0].getAttribute("data-column-id");
      if (selectedCell === "roleId") this.addPopupToggle("수정");
      if (selectedCell === "setIcon") this.setPopupToggle();
      this.selectedRowObj = { ...this.roleList[selectedObjIndex - 1] };
    },
    getRoles() {
      getRoleList().then((response) => {
        this.roleList = response.data.response.data;
        this.roleList.forEach(function (object, index) {
          object.index = index + 1;
          if (object.useYn == "Y") {
            object.useYn = "사용";
          } else {
            object.useYn = "미사용";
          }
        });
        this.filteredGridDataBack = this.roleList;
        if (this.roleNameFilter === "" && this.useYnFilter === "사용") {
          this.filteredGridData = this.roleList;
          this.filteredGridData = this.filteredGridData.filter(
            (obj) => obj.useYn.trim() === "사용"
          );
        } else {
          this.gridDataFiltering();
        }
      });
    },
    gridDataFiltering() {
      this.filteredGridData = this.filteredGridDataBack;

      if (this.useYnFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.useYn.trim() === this.useYnFilter
        );
      }
      if (this.filteredGridData.length > 0) {
        const nameFilter = [];
        this.filteredGridData.forEach((obj) => {
          if (obj.roleNm.includes(this.roleNameFilter)) {
            nameFilter.push(obj);
          }
        });
        this.filteredGridData = nameFilter;
      }
    },
  },
};
</script>
